import React from 'react';

import './topicList.css';

import { Modal, Button, Form, Spinner } from 'react-bootstrap';
import { AiOutlinePlus } from 'react-icons/ai';

import pdf from '../../assets/pdf.png';
import ppt from '../../assets/ppt.png';
import doc from '../../assets/doc.png';
import mp4 from '../../assets/mp4.png';

import { connect } from 'react-redux';

import { addResource } from '../../../../redux/actions/superAdminDashboard/superAdminSchoolActions';

import toaster from 'toasted-notes';
import 'toasted-notes/src/styles.css';

class ResourceList extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            topics: ['Velocity', 'Momentum', 'Acceleration'],
            showModal: false,
            resourceName: '',
            adding: false
        }
    }

    handleChange = (e) => {
        const { name, value } = e.target;
        this.setState({
            [name]: value
        })
    }

    handleChangeFile = (e) => {
        console.log(e.target.files[0]);
        if (e.target.files[0].size / 1024 / 1024 <= 20) {
            const f = e.target.files[0];
            this.setState({
                file: f,
                adding: false
            })
        }
        else {
            toaster.notify("Resource file cannot be more than 5mb", {
                duration: 3000
            });
        }
    }

    handleSubmit = async () => {
        this.setState({
            adding: true
        })
        const data = {
            topicName: this.props.topic.topicName,
            subjectName: this.props.subject.subjectName,
            classId: this.props.class._id,
            chapterName: this.props.chapter.chapterName,
            resourceName: this.state.resourceName + "." + this.state.file.name.split(".")[1]
        }
        var form_data = new FormData();
        form_data.append('file', this.state.file);
        const check = await this.props.addResource(form_data, data);
        console.log(check);
        if (check) {
            this.setState({
                loading: false
            })
            if (check.type === 'success') {
                toaster.notify('Resource uploaded Successfully', {
                    duration: 3000
                })
                this.setState({
                    showModal: false
                })
            }
            else {
                toaster.notify(check.error, {
                    duration: 3000
                })
            }
        }
    }

    render() {
        console.log(this.props);
        const classes = this.props.classes;
        return (
            <div className="topicList-container">

                <div className="topicList-header">
                    <Button variant="primary" onClick={() => this.setState({ showModal: true })}><AiOutlinePlus />Add Resource Material</Button>
                </div>
                {
                    classes && classes.map(clas => {
                        if (clas.className === this.props.class.className) {
                            return (
                                <>
                                    {
                                        clas.subjects.map(sub => {
                                            if (sub.subjectName === this.props.subject.subjectName) {
                                                return (
                                                    <>
                                                        {
                                                            sub.chapters.map(chapter => {
                                                                if (chapter.chapterName === this.props.chapter.chapterName) {
                                                                    return (
                                                                        <>
                                                                            {
                                                                                chapter.topics && chapter.topics.map(topic => {
                                                                                    if (topic.topicName === this.props.topic.topicName) {
                                                                                        return (
                                                                                            <>
                                                                                                {
                                                                                                    topic.resources && topic.resources.map(resource => {
                                                                                                        return (
                                                                                                            <a href={'/api/file/show/' + resource.file.filename} target="_blank" className="topicList-box">
                                                                                                                {
                                                                                                                    resource.resourceName.split('.')[1].includes('pdf')
                                                                                                                        ?
                                                                                                                        <img src={pdf} alt="icon" />
                                                                                                                        :
                                                                                                                        null
                                                                                                                }
                                                                                                                {
                                                                                                                    resource.resourceName.split('.')[1].includes('doc')
                                                                                                                        ?
                                                                                                                        <img src={doc} alt="icon" />
                                                                                                                        :
                                                                                                                        null
                                                                                                                }
                                                                                                                {
                                                                                                                    resource.resourceName.split('.')[1].includes('ppt')
                                                                                                                        ?
                                                                                                                        <img src={ppt} alt="icon" />
                                                                                                                        :
                                                                                                                        null
                                                                                                                }
                                                                                                                {
                                                                                                                    resource.resourceName.split('.')[1].includes('mp4')
                                                                                                                        ?
                                                                                                                        <img src={mp4} alt="icon" />
                                                                                                                        :
                                                                                                                        null
                                                                                                                }
                                                                                                                <div className="topic-name">
                                                                                                                    <h1>{resource.resourceName}</h1>
                                                                                                                    <p>Subject: {this.props.subject.subjectName}, Grade: {this.props.class.className}, Chapter: {this.props.chapter.chapterName}, Topic: {this.props.topic.topicName}</p>
                                                                                                                </div>
                                                                                                                <div className="topic-date">
                                                                                                                    <p>{resource.date}</p>
                                                                                                                </div>
                                                                                                            </a>
                                                                                                        )
                                                                                                    })
                                                                                                }
                                                                                            </>
                                                                                        )

                                                                                    }
                                                                                })
                                                                            }
                                                                        </>
                                                                    )
                                                                }
                                                            })
                                                        }
                                                    </>
                                                )
                                            }
                                        })
                                    }
                                </>
                            )
                        }
                    })
                }

                <Modal
                    show={this.state.showModal}
                    onHide={() => this.setState({ showModal: false })}
                    backdrop="static"
                    keyboard={false}
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Add Resource</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form.Group controlId="formBasicEmail">
                            <Form.Label>Resource Name</Form.Label>
                            <Form.Control type="text" name="resourceName" onChange={this.handleChange} />
                        </Form.Group>
                        <Form.Group controlId="formBasicEmail">
                            <Form.File id="formcheck-api-regular">
                                <Form.File.Input onChange={this.handleChangeFile} />
                            </Form.File>
                        </Form.Group>
                    </Modal.Body>
                    <Modal.Footer>
                        {
                            this.state.adding
                                ?
                                <Spinner animation="border" role="status" variant="primary">
                                    <span className="sr-only">Loading...</span>
                                </Spinner>
                                :
                                <Button variant="primary" onClick={this.handleSubmit}>Create</Button>
                        }
                    </Modal.Footer>
                </Modal>

            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        classes: state.superAdmin.classes
    };
}

function mapDispatchToProps(dispatch) {
    return {
        addResource: (form_data, data) => dispatch(addResource(form_data, data)),
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(ResourceList);