import React from 'react';
import './expandedView.css';


import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";
import Behaviour from '../../studentsBehaviour/behaviours';

import {Form} from 'react-bootstrap';

import plus from '../assets/plus.svg';
import minus from '../assets/minus.svg';
import Creativity from '../../studentCreativity/creativity';
import Entrepreneurship from '../../studentEntrepreneurship/entrepreneurship';
import Problems from '../../studentProblemSolving/problems';

class ExpandedView extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            tab : '',
            date : ''
        }
    }

    handleTileContent = (a) => {
        
        var arrayDates = [];
        this.props.stuDetails.attendance.map((item) => {
            if (item.subjectName === this.props.selected.subject) {
                arrayDates.push(item);
            }
        })
        var match=false;
        arrayDates.map((x) => {
            var date1 =  new Date(a.date).getFullYear()  + "-" + new Date(a.date).getMonth() + "-" +new Date(a.date).getDate();
            if (x.date === date1) {
                match = x
            }
        })
        if(match){
            if (match.status === 'Present') {
                return <p style = {{padding : "0.5rem 0", background : "green", color : "#fff", borderRadius : "5px"}}>{match.status}</p>
            }
            else {
                return <p style = {{padding : "0.5rem 0", background : "red", color : "#fff", borderRadius : "5px"}}>{match.status}</p>
            }
            
        }
        else{
            return null;
        }
    }

    render() {
        console.log(this.props.stuDetails);
        return (
            <div className="sidebar-cont">
                <div class="sidebar">
                    <div className={this.state.tab === 'creativity' ? "creativity active" : "creativity"} onClick = {() => this.setState({tab : 'creativity'})}>
                        <div className="icon">
                            <i class="fab fa-creative-commons-remix"></i>
                        </div>
                        <div className="icon-name">
                            <p>Creativity</p>
                        </div>
                    </div>
                    <div className={this.state.tab === 'entre' ? "Entrepreneurship active" : "Entrepreneurship"} onClick = {() => this.setState({tab : 'entre'})}>
                        <div className="icon">
                            <i class="fas fa-user-tie"></i>
                        </div>
                        <div className="icon-name">
                            <p>Entrepreneurship</p>
                        </div>
                    </div>
                    <div className={this.state.tab === 'problem' ? "ProblemSolving active" : "ProblemSolving"} onClick = {() => this.setState({tab : 'problem'})}>
                        <div className="icon">
                            <i class="far fa-question-circle"></i>
                        </div>
                        <div className="icon-name">
                            <p>Problem Solving</p>
                        </div>
                    </div>
                    <div className={this.state.tab === 'assignments' ? "Assignments active" : "Assignments"} onClick = {() => this.setState({tab : 'assignments'})}>
                        <div className="icon">
                            <i class="fas fa-book"></i>
                        </div>
                        <div className="icon-name">
                            <p>Assignments</p>
                        </div>
                    </div>
                    <div className={this.state.tab === 'practice' ? "Practices active" : "Practices"} onClick = {() => this.setState({tab : 'practice'})}>
                        <div className="icon">
                            <i class="fas fa-pen-square"></i>
                        </div>
                        <div className="icon-name">
                            <p>Practices</p>
                        </div>
                    </div>
                    <div className={this.state.tab === 'attendance' ? "Attendance active" : "Attendance"} onClick = {() => this.setState({tab : 'attendance'})}>
                        <div className="icon">
                            <i class="fas fa-users"></i>
                        </div>
                        <div className="icon-name">
                            <p>Attendance</p>
                        </div>
                    </div>
                    <div className={this.state.tab === 'behaviour' ? "Behaviour active" : "Behaviour"} onClick = {() => this.setState({tab : 'behaviour'})}>
                        <div className="icon">
                            <i class="fas fa-child"></i>
                        </div>
                        <div className="icon-name">
                            <p>Behaviour</p>
                        </div>
                    </div>
                </div>
                <div class="content">
                    {
                        this.state.tab === 'attendance'
                        ?
                        <div className = "attendance-view">
                            <Calendar tileContent = {this.handleTileContent} maxDate={new Date()} />
                        </div>
                        :
                        null
                    }

                    {/* BEHAVIOUR */}
                    {
                        this.state.tab === 'behaviour'
                        ?
                        <div className = "behaviour-view">
                            <Form.Group controlId="exampleForm.ControlInput1">
                                <Form.Label>Select Date</Form.Label>
                                <Form.Control type="date" onChange ={(e) => this.setState({date : e.target.value, showBehaviour : false},() => {
                                    this.setState({
                                        showBehaviour : true
                                    })
                                })}/>
                            </Form.Group>
                            {
                                this.state.showBehaviour
                                ?
                                <>
                                    <Behaviour 
                                    behaviours={this.props.stuDetails.behaviour}
                                    teacherId={this.props.teacher._id}
                                    date={this.state.date}/>
                                    {/* Note */}
                                    {
                                        this.props.stuDetails.behaviour.map((item) => {
                                            if (item.date === this.state.date) {
                                                return (
                                                    <Form.Group controlId="exampleForm.ControlTextarea1">
                                                        <Form.Label>Note</Form.Label>
                                                        <Form.Control as="textarea" value = {item.note} />
                                                    </Form.Group>
                                                )
                                            }
                                        })
                                    }
                                    {/* Points */}
                                    {
                                        this.props.stuDetails.behaviour.map((item) => {
                                            if (item.date === this.state.date) {
                                                var positive = 0, negative = 0;
                                                item.status.map((x) => {
                                                    if (x === 'worked hard') {
                                                        positive ++;
                                                    }
                                                    if (x === 'asked thoughtful questions') {
                                                        positive ++;
                                                    }
                                                    if (x === 'contributed to class discussions') {
                                                        positive ++;
                                                    }
                                                    if (x === 'contributed to group') {
                                                        positive ++;
                                                    }
                                                    if (x === 'following directions') {
                                                        positive ++;
                                                    }
                                                    if (x === 'attentive during instructions') {
                                                        positive ++;
                                                    }
                                                    if (x === 'assisted peer') {
                                                        positive ++;
                                                    }
                                                    if (x === 'off tasks') {
                                                        negative ++;
                                                    }
                                                    if (x === 'distracted others') {
                                                        negative ++;
                                                    }
                                                    if (x === 'improper phone using') {
                                                        negative ++;
                                                    }
                                                    if (x === 'talking during instructions') {
                                                        negative ++;
                                                    }
                                                    if (x === 'out of seat') {
                                                        negative ++;
                                                    }
                                                    if (x === 'disrespectful') {
                                                        negative ++;
                                                    }
                                                    if (x === 'unprepared for class') {
                                                        negative ++;
                                                    }
                                                })
                                                return (
                                                    <>
                                                        <div className = "point-stat">
                                                            <img src={plus} alt="plus"/>
                                                            <p>Total Positive Points : </p>
                                                            <span style = {{color : "green"}}>{positive}</span>
                                                        </div>

                                                        <div className = "point-stat">
                                                            <img src={minus} alt="plus"/>
                                                            <p>Total Negative Points : </p>
                                                            <span style = {{color : "red"}}>{negative}</span>
                                                        </div>
                                                    </>
                                                )
                                            }
                                        })
                                    }
                                </>
                                :
                                null
                            }
                            
                        </div>
                        :
                        null
                    }

                    {/* CREATIVITY */}
                    {
                        this.state.tab === 'creativity'
                        ?
                        <div className = "behaviour-view">
                            <Form.Group controlId="exampleForm.ControlInput1">
                                <Form.Label>Select Date</Form.Label>
                                <Form.Control type="date" onChange ={(e) => this.setState({date : e.target.value, showCreativity : false},() => {
                                    this.setState({
                                        showCreativity : true
                                    })
                                })}/>
                            </Form.Group>
                            {
                                this.state.showCreativity
                                ?
                                <>
                                    <Creativity 
                                     creativities={this.props.stuDetails.creativity}
                                     teacherId={this.props.teacher._id}
                                     date={this.state.date}
                                    />
                                    {/* Note */}
                                    {
                                        this.props.stuDetails.creativity.map((item) => {
                                            if (item.date === this.state.date) {
                                                return (
                                                    <Form.Group controlId="exampleForm.ControlTextarea1">
                                                        <Form.Label>Note</Form.Label>
                                                        <Form.Control as="textarea" value = {item.note} />
                                                    </Form.Group>
                                                )
                                            }
                                        })
                                    }
                                    {/* Points */}
                                    {
                                        this.props.stuDetails.creativity.map((item) => {
                                            if (item.date === this.state.date) {
                                                return (
                                                    <div className = "point-stat">
                                                        <img src={plus} alt="plus"/>
                                                        <p>Total Positive Points : </p>
                                                        <span style = {{color : "green"}}>{item.status.length}</span>
                                                    </div>
                                                )
                                            }

                                        })
                                    }
                                </>
                                :
                                null
                            }
                            
                        </div>
                        :
                        null
                    }

                    {/* Entrepreneurship */}
                    {
                        this.state.tab === 'entre'
                        ?
                        <div className = "behaviour-view">
                            <Form.Group controlId="exampleForm.ControlInput1">
                                <Form.Label>Select Date</Form.Label>
                                <Form.Control type="date" onChange ={(e) => this.setState({date : e.target.value, showEntre : false},() => {
                                    this.setState({
                                        showEntre : true
                                    })
                                })}/>
                            </Form.Group>
                            {
                                this.state.showEntre
                                ?
                                <>
                                    <Entrepreneurship 
                                    entrepreneurships = {this.props.stuDetails.entrepreneurship}
                                    teacherId={this.props.teacher._id}
                                    date={this.state.date}
                                    />
                                    {/* Note */}
                                    {
                                        this.props.stuDetails.entrepreneurship.map((item) => {
                                            if (item.date === this.state.date) {
                                                return (
                                                    <Form.Group controlId="exampleForm.ControlTextarea1">
                                                        <Form.Label>Note</Form.Label>
                                                        <Form.Control as="textarea" value = {item.note} />
                                                    </Form.Group>
                                                )
                                            }
                                        })
                                    }
                                    {/* Points */}
                                    {
                                        this.props.stuDetails.entrepreneurship.map((item) => {
                                            if (item.date === this.state.date) {
                                                return (
                                                    <div className = "point-stat">
                                                        <img src={plus} alt="plus"/>
                                                        <p>Total Positive Points : </p>
                                                        <span style = {{color : "green"}}>{item.status.length}</span>
                                                    </div>
                                                )
                                            }

                                        })
                                    }
                                </>
                                :
                                null
                            }
                            
                        </div>
                        :
                        null
                    }

                    {/* Problem Solving*/}
                    {
                        this.state.tab === 'problem'
                        ?
                        <div className = "behaviour-view">
                            <Form.Group controlId="exampleForm.ControlInput1">
                                <Form.Label>Select Date</Form.Label>
                                <Form.Control type="date" onChange ={(e) => this.setState({date : e.target.value, showProblem : false},() => {
                                    this.setState({
                                        showProblem : true
                                    })
                                })}/>
                            </Form.Group>
                            {
                                this.state.showProblem
                                ?
                                <>
                                    <Problems 
                                    problems = {this.props.stuDetails.problemSolving}
                                    teacherId={this.props.teacher._id}
                                    date={this.state.date}
                                    />
                                    {/* Note */}
                                    {
                                        this.props.stuDetails.problemSolving
                                        .map((item) => {
                                            if (item.date === this.state.date) {
                                                return (
                                                    <Form.Group controlId="exampleForm.ControlTextarea1">
                                                        <Form.Label>Note</Form.Label>
                                                        <Form.Control as="textarea" value = {item.note} />
                                                    </Form.Group>
                                                )
                                            }
                                        })
                                    }
                                    {/* Points */}
                                    {
                                        this.props.stuDetails.problemSolving
                                        .map((item) => {
                                            if (item.date === this.state.date) {
                                                return (
                                                    <div className = "point-stat">
                                                        <img src={plus} alt="plus"/>
                                                        <p>Total Positive Points : </p>
                                                        <span style = {{color : "green"}}>{item.status.length}</span>
                                                    </div>
                                                )
                                            }

                                        })
                                    }
                                </>
                                :
                                null
                            }
                            
                        </div>
                        :
                        null
                    }

                </div>
            </div>
        );
    }
}

export default ExpandedView;