import React from 'react';

import { Modal, Button, Form, Spinner, Col } from 'react-bootstrap';
import { FaCheck, FaRegTrashAlt, FaInfoCircle, FaPlus, FaCross, FaArrowCircleLeft } from "react-icons/fa";
import { IoMdClose } from 'react-icons/io';
import ReactTable from 'react-table-6';
import StudentCard from './studentCard/studentCard';
import StudentStats from './studentStats/studentStats';

import ExpandedView from './expandedView/expandedView';

class StudentTable extends React.Component{
    constructor(){
        super();
        this.state={
            editblood: "",
            editname: "",
            editemail: "",
            editphoto: [],
            editaddress:"",
            editpname: "",
            editpcontact: "",
            editpemail: "",
            editdob: "",
            editcontact: "",
            editclass: "",
            editsection: "",
            editcity: "",
            editstate: "",
            editzip: "",
            editshowImage: "",
            //Stats state
            showStats : false,
            stuDetails : {}, 
            //exapnd view
            expand : false
        }
    }
    
    handleShowEditStudent = (e) => {
        this.setState({
            editblood: e.blood,
            editname: e.name,
            editemail: e.email,
            editphoto: [],
            editaddress: e.address,
            editpname: e.pname,
            editpcontact: e.pcontact,
            editpemail: e.pemail,
            editdob: e.dob,
            editcontact: e.contact,
            editclass: e.class,
            editsection: e.section,
            editcity: e.city,
            editstate: e.state,
            editzip: e.zip,
            editshowImage: "/api/file/show/" + e.photo.filename,
            showEdit: true,
            error: ""
        })
    }

    handleShowStats = (e) => {
        this.setState({
            stuDetails : e,
            showStats : true
        })
    }

    handleExpandView = (e) => {
        this.setState({
            stuDetails : e,
            expand : true
        })
    }

    render()
    {
        const school = this.props.teacher.schoolInfo;
        
        var students = [];
        this.props.teacher.students.map(stud=>{
            if(stud.class===this.props.sub.class)
            {
                students.push(stud);
            }
        })
        console.log(this.props.sub);

        return(
            <div className="students-container">
                {
                    !this.state.showStats
                    ?
                    <>
                        <div className="students-header">
                            {
                                this.state.expand
                                ?
                                <p style ={{marginRight:"1rem", fontSize:"2rem", cursor : "pointer"}} onClick = {() => this.setState({expand : false})}><FaArrowCircleLeft /></p>
                                :
                                null
                            }
                            <div className="students-stats">
                                <h1>IIGP Students</h1>
                                <div className="officer-stats">
                                    <p>TOTAL STUDENTS : <span>{students.length}</span></p>
                                </div>
                            </div>
                        </div>
                        <div className="students-body">
                            {
                                this.state.expand
                                ?
                                null
                                :
                                <>
                                    {
                                        students.map((item) => {
                                            return (
                                                <StudentCard 
                                                student = {item} 
                                                showStats = {(value) => this.handleShowStats(value)} 
                                                showDetails = {(value) => this.handleShowEditStudent(value)} 
                                                selected = {this.props.sub}
                                                expandView = {(value) => this.handleExpandView(value)}
                                                />
                                            )
                                        })
                                    }
                                </>
                            }
                            <div className = {this.state.expand ? "grow" : "hide"}>
                                <ExpandedView stuDetails = {this.state.stuDetails} selected = {this.props.sub} teacher = {this.props.teacher}/>
                            </div>
                        </div>
                        <Modal
                            show={this.state.showEdit}
                            onHide={() => this.setState({
                                showEdit: false,
                                error: ""
                            })}
                            size="lg"
                            backdrop="static"
                            keyboard={false}
                            centered
                        >
                            <Modal.Header closeButton>
                                <Modal.Title>Student Informations </Modal.Title>
                            </Modal.Header>
                            <Modal.Body>
                                <Form>
                                    <div className="container-group">
                                        <Form.Group className="container-fluid">
                                            <div className="image-container">
                                                <p>Photo</p>
                                                <div className="image-picker">
                                                    <img htmlFor="#image-picker" src={this.state.editshowImage} />
                                                </div>
                                            </div>
                                        </Form.Group>

                                        <Form.Group className="container-fluid">
                                            <Form.Label>Name</Form.Label>
                                            <Form.Control name="editname" type="text" placeholder="Enter name" value={this.state.editname} onChange={this.handleChange} disabled/>
                                        </Form.Group>
                                    </div>
                                    <div className="container-group">
                                        <Form.Group className="container-fluid">
                                            <Form.Label>Email</Form.Label>
                                            <Form.Control name="editemail" type="text" placeholder="Enter email" value={this.state.editemail} onChange={this.handleChange} disabled />
                                        </Form.Group>

                                        <Form.Group className="container-fluid">
                                            <Form.Label>Address</Form.Label>
                                            <Form.Control name="editaddress" type="text" placeholder="Enter address" value={this.state.editaddress} onChange={this.handleChange} disabled/>
                                        </Form.Group>
                                    </div>
                                    <div className="container-group">
                                        <Form.Group className="container-fluid">
                                            <Form.Label>Date of birth</Form.Label>
                                            <Form.Control name="editdob" type="date" value={this.state.editdob} onChange={this.handleChange} disabled/>
                                        </Form.Group>

                                        <Form.Group className="container-fluid">
                                            <Form.Label>Contact No.</Form.Label>
                                            <Form.Control name="editcontact" type="text" placeholder="Enter school contact no." value={this.state.editcontact} onChange={this.handleChange} disabled/>
                                        </Form.Group>

                                        <Form.Group className="container-fluid">
                                            <Form.Label>Blood Group</Form.Label>
                                            <Form.Control as="select" value={this.state.editblood} onChange={e => { this.setState({ editblood: e.target.value }) }} disabled>
                                                <option value="" disabled hidden>Select an option</option>
                                                <option value="A+">A+</option>
                                                <option value="A-">A-</option>
                                                <option value="B+">B+</option>
                                                <option value="B-">B-</option>
                                                <option value="O+">O+</option>
                                                <option value="O-">O-</option>
                                                <option value="AB+">AB+</option>
                                                <option value="AB-">AB-</option>
                                            </Form.Control>
                                        </Form.Group>
                                    </div>
                                    <div className="container-group">
                                        <Form.Group className="container-fluid">
                                            <Form.Label>Guardian Name</Form.Label>
                                            <Form.Control name="editpname" type="text" placeholder="Enter name" value={this.state.editpname} onChange={this.handleChange} disabled/>
                                        </Form.Group>

                                        <Form.Group className="container-fluid">
                                            <Form.Label>Guardian Contact No.</Form.Label>
                                            <Form.Control name="editpcontact" type="text" placeholder="Enter contact no." value={this.state.editpcontact} onChange={this.handleChange} disabled/>
                                        </Form.Group>

                                        <Form.Group className="container-fluid">
                                            <Form.Label>Guardian Email</Form.Label>
                                            <Form.Control disabled name="editpemail" type="text" placeholder="Enter parent email" value={this.state.editpemail} onChange={this.handleChange} disabled/>
                                        </Form.Group>
                                    </div>
                                    <div className="container-group">
                                        <Form.Group className="container-fluid" >
                                            <Form.Label>Select a Class</Form.Label>
                                            <Form.Control as="select" value={this.state.editclass} onChange={e => { this.setState({ editclass: e.target.value }) }} disabled>
                                                <option value="" disabled hidden>Select a class</option>
                                                {
                                                    school && school.classes.map(clas => {
                                                        return (
                                                            <option value={clas.name}>{clas.name}</option>
                                                        )
                                                    })
                                                }
                                            </Form.Control>
                                        </Form.Group>
                                        <Form.Group className="container-fluid">
                                            <Form.Label>Choose Section</Form.Label>
                                            <Form.Control as="select" value={this.state.editsection} onChange={e => { this.setState({ editsection: e.target.value }) }} disabled>
                                                <option value="" disabled hidden>Select a section</option>
                                                {
                                                    school && school.classes.map(clas => {
                                                        if (clas.name === this.state.editclass) {
                                                            return (
                                                                <>
                                                                    {
                                                                        clas.sections.map(sec => {
                                                                            return (
                                                                                <option value={sec.name}>{sec.name}</option>
                                                                            )
                                                                        })
                                                                    }
                                                                </>
                                                            )
                                                        }
                                                    })
                                                }
                                            </Form.Control>
                                        </Form.Group>

                                    </div>
                                    <div className="container-group">
                                        <Form.Group className="container-fluid">
                                            <Form.Label>City</Form.Label>
                                            <Form.Control name="editcity" type="text" placeholder="Enter city" value={this.state.editcity} onChange={this.handleChange} disabled/>
                                        </Form.Group>
                                        <Form.Group className="container-fluid">
                                            <Form.Label>State</Form.Label>
                                            <Form.Control name="editstate" type="text" placeholder="Enter state" value={this.state.editstate} onChange={this.handleChange} disabled/>
                                        </Form.Group>
                                        <Form.Group className="container-fluid">
                                            <Form.Label>Zip</Form.Label>
                                            <Form.Control name="editzip" type="text" placeholder="Enter zip" value={this.state.editzip} onChange={this.handleChange} disabled/>
                                        </Form.Group>
                                    </div>
                                </Form>
                            </Modal.Body>
                        </Modal>
                    </>
                    :
                    <StudentStats sub = {this.props.sub.subject} stuDetails = {this.state.stuDetails} goBack = {(value) => this.setState({showStats : value})}/>                     
                }
            </div>
        )
    }
}


export default StudentTable;