import React from "react";
import { connect } from "react-redux";
import "./library.css";
import folderImg from "../assets/folderIcon.svg";
import LibraryTable from "./libraryTable";

class Library extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			showSubList: false,
			showChapterList: false,
			showTopicList: false,
			ShowQuizMaker: false,
		};
	}

	handleBox = (e) => {
		this.setState({
			current: e,
			showList: true,
		});
	};

	render() {
		var teacher = this.props.teacher;
		return (
			<div className='quiz-container'>
				{this.state.showList ? (
					<LibraryTable sub={this.state.current} />
				) : (
					<div className='chooseClass-container'>
						{teacher &&
							teacher.subjects.map((sub) => {
								return (
									<div className='classes-cardFolder-container' onClick={() => this.handleBox(sub)}>
										<div className='classes-cardFolder-img'>
											<img src={folderImg} alt='folder-icon' />
										</div>
										<div className='classes-cardFolder-name'>
											<p>
												{sub.class} {sub.section}
											</p>
											<p>{sub.subject}</p>
										</div>
									</div>
								);
							})}
					</div>
				)}
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		teacher: state.teacher.profileData,
	};
}

export default connect(mapStateToProps)(Library);
