import React from "react";
import { connect } from 'react-redux';

import SuperAdminDashboard from './superAdminDashboard/superAdminDashboard';
import SchoolAdminDashboard from './schoolAdminDashboard/schoolAdminDashboard';
import DistrictDirectorDashboard from './districtDirectorDashboard/districtDirectorDashboard';
import TeacherDashboard from "./teacherDashboard/teacherDashboard";
import StudentDashboard from "./studentWebsite/studentDashboard";
import ParentDashboard from "./parentWebsite/parentDashboard";

class Layout extends React.Component {

	render() {
		console.log(this.props.auth);
		return (
			<div className="layout">
				{
					this.props.auth.currentUser && this.props.auth.currentUser.role === "SuperAdmin"
					?
					<SuperAdminDashboard />
					:
					null
				}
				{
					this.props.auth.currentUser && this.props.auth.currentUser.role === "SchoolAdmin"
					?
					<SchoolAdminDashboard />
					:
					null
				}
				{
					this.props.auth.currentUser && this.props.auth.currentUser.role === "DistrictOfficer"
					?
					<DistrictDirectorDashboard />
					:
					null
				}
				{
					this.props.auth.currentUser && this.props.auth.currentUser.role === "Teacher"
					?
					<TeacherDashboard />
					:
					null
				}
				{
					this.props.auth.currentUser && this.props.auth.currentUser.role === "Student"
					?
					<StudentDashboard />
					:
					null
				}
				{
					this.props.auth.currentUser && this.props.auth.currentUser.role === "Parent"
					?
					<ParentDashboard />
					:
					null
				}
			</div>
		);
	}
}

const mapStateToProps = ({ auth }) => {
	return { auth }
}

export default connect(mapStateToProps)(Layout);
