import React from "react";

import { Modal, Button, Form, Spinner, Col } from "react-bootstrap";
import { FaCheck, FaRegTrashAlt, FaInfoCircle, FaPlus, FaCross } from "react-icons/fa";

import ReactTable from "react-table-6";
import addImg from "../assets/add.svg";

import toaster from "toasted-notes";
import "toasted-notes/src/styles.css";
import { connect } from "react-redux";
import axios from "axios";
import { addResource, deleteResource } from "../../../redux/actions/teacherDashboard/teacherActions";

class LibraryTable extends React.Component {
	constructor() {
		super();
		this.state = {
			showAdd: false,
			showDelete: false,
			classes: [],
			resourceName: "",
			classes: [],
			chapterName: "",
			topicName: "",
			file: {},
			adding: false,
			resource: {},
		};
	}

	async componentDidMount() {
		var res = await axios.get("/api/schoolAdmin/iigpClasses");
		if (res.data !== null) {
			console.log(res.data);
			this.setState({
				classes: res.data,
			});
		}
	}

	handleChange = (e) => {
		const { name, value } = e.target;
		this.setState({
			[name]: value,
		});
	};

	handleChangeFile = (e) => {
		console.log(e.target.files[0]);
		if (e.target.files[0].size / 1024 / 1024 <= 20) {
			const f = e.target.files[0];
			this.setState({
				file: f,
				adding: false,
			});
		} else {
			toaster.notify("Resource file cannot be more than 5mb", {
				duration: 3000,
			});
		}
	};

	handleSubmit = async () => {
		this.setState({
			adding: true,
		});
		const data = {
			schoolId: this.props.teacher.schoolInfo._id,
			topicName: this.state.topicName,
			subjectName: this.props.sub.subject,
			sectionName: this.props.sub.section,
			className: this.props.sub.class,
			chapterName: this.state.chapterName,
			resourceName: this.state.resourceName + "." + this.state.file.name.split(".")[1],
			teacherId: this.props.teacher._id,
		};
		var form_data = new FormData();
		form_data.append("file", this.state.file);
		const check = await this.props.addResource(form_data, data);
		console.log(check);
		if (check) {
			this.setState({
				loading: false,
			});
			if (check.type === "success") {
				toaster.notify("Resource uploaded Successfully", {
					duration: 3000,
				});
				this.setState({
					showAdd: false,
				});
			} else {
				toaster.notify(check.error, {
					duration: 3000,
				});
			}
		}
	};

	handleDeleteResource = async () => {
		console.log(this.state.resource);
		this.setState({
			deleting: true,
		});
		const data = {
			schoolId: this.props.teacher.schoolInfo._id,
			subjectName: this.props.sub.subject,
			sectionName: this.props.sub.section,
			className: this.props.sub.class,
			resource: this.state.resource,
			teacherId: this.props.teacher._id,
		};
		const check = await this.props.deleteResource(data);
		console.log(check);
		if (check) {
			this.setState({
				loading: false,
			});
			if (check.type === "success") {
				toaster.notify("Resource deleted Successfully", {
					duration: 3000,
				});
				this.setState({
					showDelete: false,
				});
			} else {
				toaster.notify(check.error, {
					duration: 3000,
				});
			}
		}
	};

	render() {
		var list = [];
		this.props.teacher.schoolInfo.classes.map((clas) => {
			if (clas.name === this.props.sub.class) {
				clas.sections.map((sec) => {
					if (sec.name === this.props.sub.section) {
						sec.subjects.map((sub) => {
							if (sub.name === this.props.sub.subject) {
								list = sub.resources;
							}
						});
					}
				});
			}
		});
		console.log(list);

		var data = [
			{
				resourceName : 'Book.pdf',
				title : 'Understanding Mathematics for Children',
				page : '180',
				isbn : '978-3-16-148410-0',
				file : list[0].file,
				date : list[0].date
			}
		];
		const columns = [
			{
				Header: "Book File",
				accessor: "resourceName",
				sortable: false,
				filterable: true,
				style: {
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
				},
			},
			{
				Header: "Title",
				accessor: "title",
				sortable: false,
				filterable: true,
				style: {
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
				},
			},
			{
				Header: "Total Pages",
				accessor: "page",
				sortable: false,
				filterable: true,
				style: {
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
				},
			},
			{
				Header: "ISBN Code",
				accessor: "isbn",
				sortable: false,
				filterable: true,
				style: {
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
				},
			},
			{
				Header: "Date",
				accessor: "date",
				sortable: false,
				filterable: true,
				style: {
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
				},
			},
			{
				Header: "Actions",
				Cell: (props) => {
					return (
						<div
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}>
							<Button
								onClick={() => {
									window.open("/api/file/show/" + props.original.file.filename);
								}}
								variant={"outline-info"}
								className='action-buttons'>
								View
							</Button>
						</div>
					);
				},
				sortable: false,
				filterable: false,
				minWidth: 150,
				style: {
					color: "#fff",
					display: "flex",
					alignItems: "center",
				},
			},
		];
		return (
			<div className='students-container'>
				<div className='students-header'>
					<div className='students-stats'>
						<h1>School Library</h1>
						{/* <div className='officer-stats'>
							<p>
								TOTAL LESSON PLANS: <span>{data.length}</span>
							</p>
						</div> */}
					</div>
					<div className='students-add'></div>
				</div>
				<div className='students-body'>
					<ReactTable
						data={data}
						columns={columns}
						defaultPageSize={10}
						showPaginationTop
						showPaginationBottom={false}
						className='-striped -highlight'
					/>
				</div>
				<Modal
					show={this.state.showAdd}
					onHide={() =>
						this.setState({
							showAdd: false,
							error: "",
						})
					}
					backdrop='static'
					keyboard={false}
					centered>
					<Modal.Header closeButton>
						<Modal.Title>Add new lesson plan</Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<Form.Group>
							<Form.Label>Choose a chapter</Form.Label>
							<Form.Control
								as='select'
								value={this.state.subjectName}
								onChange={(e) => {
									this.setState({ chapterName: e.target.value });
								}}>
								<option value=''>Select a chapter</option>
								{this.state.classes.map((clas) => {
									if (clas.className === this.props.sub.class) {
										return (
											<>
												{clas.subjects.map((sub) => {
													console.log(this.props.sub.subject);
													if (sub.subjectName === this.props.sub.subject) {
														console.log(this.props.sub.subject);
														return (
															<>
																{sub.chapters.map((c) => {
																	return <option value={c.chapterName}>{c.chapterName}</option>;
																})}
															</>
														);
													}
												})}
											</>
										);
									}
								})}
							</Form.Control>
						</Form.Group>
						{this.state.chapterName ? (
							<Form.Group>
								<Form.Label>Choose a topic</Form.Label>
								<Form.Control
									as='select'
									value={this.state.subjectName}
									onChange={(e) => {
										this.setState({ topicName: e.target.value });
									}}>
									<option value=''>Select a topic</option>
									{this.state.classes.map((clas) => {
										if (clas.className === this.props.sub.class) {
											return (
												<>
													{clas.subjects.map((sub) => {
														console.log(this.props.sub.subject);
														if (sub.subjectName === this.props.sub.subject) {
															console.log(this.props.sub.subject);
															return (
																<>
																	{sub.chapters.map((c) => {
																		if (c.chapterName === this.state.chapterName) {
																			return (
																				<>
																					{c.topics.map((t) => {
																						return <option value={t.topicName}>{t.topicName}</option>;
																					})}
																				</>
																			);
																		}
																	})}
																</>
															);
														}
													})}
												</>
											);
										}
									})}
								</Form.Control>
							</Form.Group>
						) : null}
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Resource Name</Form.Label>
							<Form.Control type='text' name='resourceName' onChange={this.handleChange} />
						</Form.Group>
						<Form.Group controlId='formBasicEmail'>
							<Form.File id='formcheck-api-regular'>
								<Form.File.Input onChange={this.handleChangeFile} />
							</Form.File>
						</Form.Group>
					</Modal.Body>
					<Modal.Footer>
						<Button onClick={this.handleSubmit} className='submit-action' variant='success'>
							<FaCheck />
							Add
						</Button>
					</Modal.Footer>
				</Modal>
				<Modal
					show={this.state.showDelete}
					onHide={() =>
						this.setState({
							showDelete: false,
						})
					}
					size='sm'
					backdrop='static'
					keyboard={false}
					centered>
					<Modal.Header closeButton>
						<Modal.Title>Delete Resource</Modal.Title>
					</Modal.Header>
					<Modal.Footer>
						<Button onClick={this.handleDeleteResource} className='submit-action' variant='danger'>
							<FaCheck />
							Delete
						</Button>
					</Modal.Footer>
				</Modal>
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		teacher: state.teacher.profileData,
	};
}

function mapDispatchToProps(dispatch) {
	return {
		addResource: (form_data, data) => dispatch(addResource(form_data, data)),
		deleteResource: (data) => dispatch(deleteResource(data)),
	};
}

export default connect(mapStateToProps, mapDispatchToProps)(LibraryTable);
