import React from 'react';
import './participation.css';
import { connect } from 'react-redux'

import "react-table-6/react-table.css";

import toaster from 'toasted-notes';
import 'toasted-notes/src/styles.css';
import ParticipationTable from './participationTable';
import folderImg from '../assets/folderIcon.svg';

class Participation extends React.Component {

    constructor() {
        super();
        
        this.state = {
            showTable:false,
        }
    }

    handleBox=(e)=>{
        this.setState({
            current : e,
            showTable : true
        })
    }

    render() {
        var teacher = this.props.teacher;
        return (
            <div className="students-container">
            {
                !this.state.showTable
                ?
                <div className="chooseClass-container">
                    {
                        teacher && teacher.subjects.map(sub=>{
                            return(
                                
                                <div className="classes-cardFolder-container" onClick={() => this.handleBox(sub)}>
                                    <div className="classes-cardFolder-img">
                                        <img src={folderImg} alt="folder-icon" />
                                    </div>
                                    <div className="classes-cardFolder-name">
                                        <p>{sub.class} {sub.section}</p>
                                        <p>{sub.subject}</p>
                                    </div>
                                </div>
                            )
                        })
                    }
                </div>
                :
                <ParticipationTable teacher={teacher} sub={this.state.current}/>
            }
            </div>
        )
    }
}
function mapStateToProps(state) {
    return {
        teacher: state.teacher.profileData
    };
}

export default connect(mapStateToProps)(Participation);