import React from 'react';

import { Modal, Button, Form, Spinner, Col } from 'react-bootstrap';
import { FaCheck, FaRegTrashAlt, FaInfoCircle, FaPlus, FaCross } from "react-icons/fa";
import { IoMdClose } from 'react-icons/io';
import ReactTable from 'react-table-6';
class StudentTable extends React.Component{
    constructor(){
        super();
        this.state={
            openAddQues : false,
            ques : '',
            questionList : [],
            editblood: "",
            editname: "",
            editemail: "",
            editphoto: [],
            editaddress:"",
            editpname: "",
            editpcontact: "",
            editpemail: "",
            editdob: "",
            editcontact: "",
            editclass: "",
            editsection: "",
            editcity: "",
            editstate: "",
            editzip: "",
            editshowImage: "",
        }
    }
    
    handleShowEditStudent = (e) => {
        this.setState({
            editblood: e.blood,
            editname: e.name,
            editemail: e.email,
            editphoto: [],
            editaddress: e.address,
            editpname: e.pname,
            editpcontact: e.pcontact,
            editpemail: e.pemail,
            editdob: e.dob,
            editcontact: e.contact,
            editclass: e.class,
            editsection: e.section,
            editcity: e.city,
            editstate: e.state,
            editzip: e.zip,
            editshowImage: "/api/file/show/" + e.photo.filename,
            showEdit: true,
            error: ""
        })
    }

    handleChange = (e) => {
        var {name, value} = e.target;
        this.setState({
            [name] : value
        })
    }

    handleSubmitQues = () => {
        this.setState({
            questionList : [...this.state.questionList, this.state.ques],
        })
    }

    render()
    {
        const school = this.props.teacher.schoolInfo;
        
        var students=[];
        this.props.teacher.students.map(stud=>{
            if(stud.class===this.props.class)
            {
                students.push(stud);
            }
        })
        const data = students;
        const columns = [{
            Header: "Photo",
            Cell: (props) => {
                return (
                    <div style={{
                        width:"100%",
                        display: "flex",
                        alignItems: "center",
                        justifyContent: 'center',
                    }}>
                        <img className="student-photo" src={"/api/file/show/"+props.original.photo.filename} />
                    </div>
                );
            },
            sortable: false,
            filterable: false,
            width: 100,
            style: {
                display: "flex",
                alignItems: "center",

            },
        },
            {
                Header: "Name",
                accessor: "name",
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                    justifyContent: 'center',
                },
            },
            {
                Header: "Date of Birth",
                accessor: "dob",
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                    justifyContent: 'center',
                },
            },
            {
                Header: "Email",
                accessor: "email",
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                    justifyContent: 'center',
                },
            },
            {
                Header: "Contact No.",
                accessor: "contact",
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                    justifyContent: 'center',
                },
            },
            {
                Header: "Actions",
                Cell: (props) => {
                    return (
                        <div
                            style={{
                                width: "100%",
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                            }}
                        >
                            {/* <Button onClick={() => {
                                this.setState({
                                    showView: true,
                                    item: props.original,
                                    error: "",
                                    showAddAdmin: false,
                                    showEditAdmin: false,
                                    showAddOfficer: false
                                })
                            }} variant={"outline-dark"} className="action-buttons">Stats</Button> */}
                            <Button onClick={() => this.handleShowEditStudent(props.original)} variant={"outline-info"} className="action-buttons">Details</Button>
                        </div>
                    );
                },
                sortable: false,
                filterable: false,
                minWidth: 150,
                style: {
                    color: "#fff",
                    display: "flex",
                    alignItems: "center",

                },
            },
        ];
        return(
            <div className="students-container">
                <div className="students-header">
                    <div className="students-stats">
                        <h1>IIGP Students</h1>
                        <div className="officer-stats">
                            <p>TOTAL STUDENTS: <span>{students.length}</span></p>
                        </div>
                    </div>
                    <div className = "students-btns">
                        <Button variant = "primary" onClick = {() => this.setState({openAddQues : true})}><FaPlus /> Add Questions</Button>
                       
                    </div>
                </div>
                <div className="students-body">
                    <ReactTable
                        data={data}
                        columns={columns}
                        defaultPageSize={10}
                        showPaginationTop
                        showPaginationBottom={false}
                        className="-striped -highlight"
                    />
                </div>

                {/* ADD PATH NEW QUESTION */}
                <Modal
                    show={this.state.openAddQues}
                    onHide={() => this.setState({
                        openAddQues: false,
                    })}
                    size="lg"
                    backdrop="static"
                    keyboard={false}
                    centered
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Add New Career Question </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form.Group className="container-fluid">
                            <Form.Label>Type your question</Form.Label>
                            <Form.Control name="ques" type="text" placeholder="Question..." value={this.state.ques} onChange={this.handleChange} />
                            
                        </Form.Group>
                        <Form.Group className="container-fluid">
                            <Button variant = "success" onClick = {this.handleSubmitQues}>Submit</Button>
                        </Form.Group>
                        <hr />

                        {
                            this.state.questionList.map((question) => {
                                return (
                                    <Form.Group className="container-fluid">
                                        <Form.Control className="ques-wrapper" type="text" value={question} disabled/>
                                    </Form.Group>
                                )
                            })
                        }

                    </Modal.Body>
                </Modal>

                <Modal
                    show={this.state.showEdit}
                    onHide={() => this.setState({
                        showEdit: false,
                        error: ""
                    })}
                    size="lg"
                    backdrop="static"
                    keyboard={false}
                    centered
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Student Career Path</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>

                    </Modal.Body>
                </Modal>
                <Modal
                    show={this.state.showView}
                    onHide={() => this.setState({
                        showView: false,
                        error: ""
                    })}
                    size="lg"
                    backdrop="static"
                    keyboard={false}
                    centered
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Academic Informations </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                    </Modal.Body>
                </Modal>
            </div>
        )
    }
}


export default StudentTable;