import React from "react";

import { Modal, Button, Form, Spinner, Col } from "react-bootstrap";
import { FaCheck, FaRegTrashAlt, FaInfoCircle, FaPlus, FaCross } from "react-icons/fa";
import { IoMdClose } from "react-icons/io";
import toaster from "toasted-notes";
import "toasted-notes/src/styles.css";
import ReactTable from "react-table-6";
import { addBehaviour, addBehaviourNote } from "../../../redux/actions/teacherDashboard/teacherActions";
import { connect } from "react-redux";

class ParticipationTable extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			data : []
		};
	}

	handleChange = (e) => {
		this.setState({
			note: e.target.value,
		});
	};

	handleSetFakeData = () => {
		var data = [
			{
				title : 'Assignment of Mathematics Page 20',
				type : 'Assignment',
				status : false,
				date : '11/11/2020'
			},
			{
				title : 'First Unit Examination',
				type : 'Examination',
				status : true,
				date : '15/11,2020'
			},
		]

		this.setState({
			data : data
		})
	}

	render() {

		const school = this.props.teacher.schoolInfo;
		var students = [];
		this.props.teacher.students.map((stud) => {
			if (stud.class === this.props.sub.class) {
				students.push(stud);
			}
		});

		const data = this.state.data;
		const columns = [
			
			{
				Header: "Title",
				accessor: "title",
				sortable: false,
				filterable: true,
				minWidth : 200,
				style: {
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
				},
			},
			{
				Header: "Type",
				accessor: "type",
				sortable: false,
				filterable: true,
				style: {
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
				},
			},
			{
				Header: "Status",
				accessor: "status",
                Cell: (props) => {
                    if (props.original.status == true) {
						return (
							<div
								style={{
									width: "100%",
									display: "flex",
									alignItems: "center",
									justifyContent: "center",
								}}
							>
								<p style ={{background : "green", padding : "0.2rem 0.5rem", borderRadius : "5px"}}>Attempted</p>
							</div>
						);
					}
					else {
						return (
							<div
								style={{
									width: "100%",
									display: "flex",
									alignItems: "center",
									justifyContent: "center",
								}}
							>
								<p style ={{background : "red", padding : "0.2rem 0.5rem", borderRadius : "5px"}}>Not Attempted</p>
							</div>
						);
					}
                },
                sortable: false,
                filterable: false,
                minWidth: 150,
                style: {
                    color: "#fff",
                    display: "flex",
                    alignItems: "center",

                },
            },
			{
				Header: "Date",
				accessor: "date",
				sortable: false,
				filterable: true,
				style: {
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
				},
			},
		];
		return (
			<div className='students-container'>
				<div className='students-header'>
					<div className='students-stats'>
						<h1>IIGP Students</h1>
						<div className='officer-stats'>
							<p>
								TOTAL STUDENTS: <span>{students.length}</span>
							</p>
						</div>
					</div>
				</div>
				
				<div className='students-participation-body'>
					<div className = "stu-list">
						{
							students.length > 0 && students.map((item) => {
								return (
									<div className = "stu-label" onClick = {() => this.handleSetFakeData()}>
										<img src={"/api/file/show/" + item.photo.filename} alt="dp"/>
										<p>{item.name}</p>
									</div>
								)
							})
						}
						
					</div>
					<div className = "participation-tbl">
						<ReactTable
							data={data}
							columns={columns}
							defaultPageSize={10}
							showPaginationTop
							showPaginationBottom={false}
							className='-striped -highlight'
						/>
					</div>
				</div>
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		teacher: state.teacher.profileData,
	};
}

export default connect(mapStateToProps, null)(ParticipationTable);
